/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Ubuntu:400,700&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'ArcadeClassic';
  src: url("/fonts/ArcadeClassic.eot");
  src: url("/fonts/ArcadeClassic.eot?#iefix") format("embedded-opentype"), url("/fonts/ArcadeClassic.woff2") format("woff2"), url("/fonts/ArcadeClassic.woff") format("woff"), url("/fonts/ArcadeClassic.ttf") format("truetype"), url("/fonts/ArcadeClassic.svg#ArcadeClassic") format("svg");
  font-weight: normal;
  font-style: normal; }

html, body {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

img {
  width: 50px;
  height: 50px; }

header {
  background: linear-gradient(rgba(0, 0, 0, 0.5)), url(/images/mobile-background.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  padding: 20px; }
  @media (min-width: 600px) {
    header {
      display: flex;
      height: 60px;
      justify-content: center; } }
  @media (min-width: 1240px) {
    header {
      align-items: center;
      height: 80px; } }
  header p {
    color: white;
    text-align: center;
    font-size: 26px;
    font-family: 'ArcadeClassic';
    font-weight: normal;
    font-style: normal;
    word-spacing: 30px;
    filter: blur(0.007em);
    text-shadow: 0 0 0.15em #1da9cc;
    animation: shake 5s linear forwards infinite; }
    @media (min-width: 600px) {
      header p {
        width: 500px;
        font-size: 30px; } }
    @media (min-width: 1240px) {
      header p {
        width: 100%;
        font-size: 38px; } }

@keyframes shake {
  5%, 15%, 25%, 35%, 55%, 65%, 75%, 95% {
    filter: blur(0.018em);
    transform: translateY(0.05em) rotate(0deg); }
  10%, 30%, 40%, 50%, 70%, 80%, 90% {
    filter: blur(0.01em);
    transform: translateY(-0.05em) rotate(0deg); }
  20%, 60% {
    filter: blur(0.03em);
    transform: translate(-0.05em, 0.05em) rotate(0deg); }
  45%, 85% {
    filter: blur(0.03em);
    transform: translate(0.05em, -0.05em) rotate(0deg); }
  100% {
    filter: blur(0.007em);
    transform: translate(0) rotate(-0.5deg); } }

body {
  background-color: #204056; }

.exp {
  display: flex;
  flex-direction: column; }
  .exp h2 {
    color: #c1c730;
    font-family: 'ArcadeClassic';
    font-weight: normal;
    font-style: normal;
    font-size: 20px;
    text-align: center;
    word-spacing: 5px;
    padding: 15px 0; }
    @media (min-width: 600px) {
      .exp h2 {
        font-size: 26px; } }
    @media (min-width: 1240px) {
      .exp h2 {
        font-size: 30px; } }
  .exp .video {
    align-self: center;
    width: 350px;
    padding-bottom: 10px; }
    @media (min-width: 600px) {
      .exp .video {
        width: 450px; } }
    @media (min-width: 1240px) {
      .exp .video {
        width: 550px; } }
    .exp .video iframe {
      height: 200px;
      width: 100%; }
      @media (min-width: 600px) {
        .exp .video iframe {
          height: 300px; } }
      @media (min-width: 1240px) {
        .exp .video iframe {
          height: 400px; } }

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0; }
  @media (min-width: 1240px) {
    .game {
      padding: 30px 0; } }

.score-board {
  display: flex;
  width: 200px;
  font-family: 'ArcadeClassic';
  font-weight: normal;
  font-style: normal;
  font-size: 30px;
  color: white;
  border: 2px dotted #c1c730;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  position: relative; }
  @media (min-width: 600px) {
    .score-board {
      font-size: 39px;
      width: 250px; } }
  @media (min-width: 1240px) {
    .score-board {
      font-size: 45px;
      width: 300px;
      padding: 30px 0; } }
  .score-board .badge {
    color: white;
    background-color: #204056;
    border: 2px solid lightcoral;
    font-size: 20px;
    padding: 2px 10px; }
    @media (min-width: 600px) {
      .score-board .badge {
        font-size: 26px; } }
    @media (min-width: 1240px) {
      .score-board .badge {
        font-size: 30px; } }
  .score-board #user-label {
    position: absolute;
    top: 50;
    left: -35px;
    border-radius: 5px; }
  .score-board #computer-label {
    position: absolute;
    top: 50;
    right: -25px;
    border-radius: 5px; }

.result {
  padding: 30px 0; }
  .result #outcome {
    font-family: 'Ubuntu', sans-serif;
    font-weight: bold;
    color: white;
    font-size: 17px; }
    @media (min-width: 600px) {
      .result #outcome {
        font-size: 23px; } }
    @media (min-width: 1240px) {
      .result #outcome {
        font-size: 27px; } }

.game-choices {
  display: inline-grid;
  padding: 20px 0;
  grid-template-columns: 33% 33% 33%;
  grid-template-rows: 33% 33% 33%;
  width: 80%;
  height: 200px; }
  @media (min-width: 600px) {
    .game-choices {
      height: 300px; } }
  @media (min-width: 1240px) {
    .game-choices {
      grid-template-columns: 20% 20% 20% 20% 20%;
      grid-template-rows: 100%; } }
  .game-choices .choices {
    margin: auto; }

#rock {
  grid-column: 1 / 2;
  grid-row: 1 / 2; }
  @media (min-width: 1240px) {
    #rock {
      grid-column: 1 / 2;
      grid-row: 1 / 1; } }

#paper {
  grid-column: 3 / 4;
  grid-row: 1 / 2; }
  @media (min-width: 1240px) {
    #paper {
      grid-column: 2 / 3;
      grid-row: 1 / 1; } }

#scissors {
  grid-column: 1 / 2;
  grid-row: 3 / 4; }
  @media (min-width: 1240px) {
    #scissors {
      grid-column: 3 / 4;
      grid-row: 1 / 1; } }

#lizard {
  grid-column: 3 / 4;
  grid-row: 3 / 4; }
  @media (min-width: 1240px) {
    #lizard {
      grid-column: 4 / 5;
      grid-row: 1 / 1; } }

#spock {
  grid-column: 2 / 3;
  grid-row: 2 / 3; }
  @media (min-width: 1240px) {
    #spock {
      grid-column: 5 / 6;
      grid-row: 1 / 1; } }

.choice {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background: #1d394d;
  border-radius: 50%;
  padding: 15px;
  margin: 0; }
  .choice img {
    width: 45px;
    height: 45px; }
    @media (min-width: 600px) {
      .choice img {
        width: 55px;
        height: 55px; } }
    @media (min-width: 1240px) {
      .choice img {
        width: 65px;
        height: 65px; } }

.user-win {
  border: 2px solid #39ff14;
  border-radius: 50%; }

.user-loss {
  border: 2px solid #FF0000;
  border-radius: 50%; }

.user-draw {
  border: 2px solid #DCDDDD;
  border-radius: 50%; }

footer {
  background: #122431;
  height: 100px;
  font-family: 'ArcadeClassic';
  font-weight: normal;
  font-style: normal;
  font-size: 16px;
  color: #D2D2D2; }
  @media (min-width: 600px) {
    footer {
      height: 150px;
      font-size: 22px; } }
  @media (min-width: 1240px) {
    footer {
      font-size: 26px; } }
  footer span {
    display: inline-block;
    vertical-align: middle;
    padding: 10px;
    word-spacing: 5px; }
    @media (min-width: 600px) {
      footer span {
        padding: 20px; } }
    footer span a, footer span a:visited {
      color: #D2D2D2; }
  footer p {
    font-size: 12px;
    padding: 0 10px; }
    @media (min-width: 600px) {
      footer p {
        font-size: 16px; } }
